.modal{
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgb(0, 0, 0,0.4);
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
   opacity: 0;
   pointer-events: none;
    transition: 0.5s;
    z-index: 9999;
}

.modal.active{
    opacity: 1;
    pointer-events: all;
}

.wrapper{
background-color: white;
    border-radius: 15px;
    padding: 20px;
    transform: scale(0.5);
    transition:  0.4s all;
    width: 50vw;
    height: 50vh;
}
.wrapper.active{
    transform: scale(1);
   background: rgba(30, 27, 30, 1.00);
}